import "./style.scss";

const modes = {
  single: "single",
  range: "range"
}

class Calendar {
  constructor({ classId, mode = "single", onSelected, onClean }) {
    this.classId = classId;
    this.mode = mode;
    this.onSelected = onSelected;
    this.onClean = onClean;

    if (mode !== "single" && mode !== "range")
      throw new Error("init Calendar error: param `mode` should be `single` or `range`");

    this.state = {
      currentDate: Date.now(),
      singleDateSelected: null,
      range: {
        dateLeft: null,
        dateRight: null,
      }
    }

    this.classCellDisabled = "calendar__content-cell_disabled";
    this.classCellHidden = "calendar__content-cell_hidden";
    this.classCellSingleSelected = "calendar__content-cell_select-one";
    this.classCellRangeLeftSelected = "calendar__content-cell_select-range-left";
    this.classCellRangeRightSelected = "calendar__content-cell_select-range-right";
    this.classCellRangeMiddleSelected = "calendar__content-cell_select-range-middle";

    this._domInit();
    this._render();
  }

  _domInit() {
    this.headerDateElem = document.querySelector(`.${this.classId} .calendar__header-info`);
    this.headerNextMonthBtnElem = document.querySelector(`.${this.classId} .calendar__header-btn-right`);
    this.headerPreviousMonthBtnElem = document.querySelector(`.${this.classId} .calendar__header-btn-left`);

    this.contentDays = document.querySelectorAll(`.${this.classId} .calendar__content-cell`);

    this.actionClean = document.querySelector(`.${this.classId} .calendar__action-clean`);
    this.actionApply = document.querySelector(`.${this.classId} .calendar__action-apply`);

    this._setupHeaderEvents();
    this._setupCalendarEvents();
    this._setupActionEvents();
  }

  _setupHeaderEvents() {
    this.headerNextMonthBtnElem.addEventListener("click", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this._setNextMonth()
    });

    this.headerPreviousMonthBtnElem.addEventListener("click", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this._setPreviousMonth()
    });
  }

  _setupActionEvents() {
    this.actionClean.addEventListener("click", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this._cleanSelected();
    });

    this.actionApply.addEventListener("click", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this._applySelected();
    })
  }

  _cleanSelected() {
    this.setState({
      singleDateSelected: null,
      range: { dateLeft: null, dateRight: null }
    });

    this.onClean && this.onClean();
  }

  _applySelected() {
    if (this.mode === modes.single && this.state.singleDateSelected !== null) {
      const resultDate = new Date(this.state.singleDateSelected);
      this.onSelected && this.onSelected(resultDate);
    }

    const range = this.state.range;
    if (
      this.mode === modes.range
      && range.dateLeft !== null
      && range.dateRight !== null
    ) {
      this.onSelected && this.onSelected(range);
    }
  }

  _selectSingleDay(date) {
    this.setState({ singleDateSelected: date });
  }

  _selectRangeDay(date) {
    const range = this.state.range;

    if (range.dateLeft === null) {
      this.setState({ range: { dateLeft: date, dateRight: null } });
      return;
    }

    if (range.dateRight === null && this._isDateLessThenOther(range.dateLeft, date)) {
      this.setState({ range: { dateLeft: range.dateLeft, dateRight: date } });
      return;
    }

    if (this._isDateLessThenOther(date, range.dateLeft)) {
      this.setState({ range: { dateLeft: date, dateRight: range.dateRight } });
      return;
    }

    if (this._isDateLessThenOther(range.dateRight, date)) {
      this.setState({ range: { dateLeft: range.dateLeft, dateRight: date } });
      return;
    }

    const dateTimestamp = new Date(date).getTime();
    if (new Date(range.dateRight).getTime() - dateTimestamp <= dateTimestamp - new Date(range.dateLeft).getTime()) {
      this.setState({ range: { dateLeft: range.dateLeft, dateRight: date } });
      return;
    } else {
      this.setState({ range: { dateLeft: date, dateRight: range.dateRight } });
      return;
    }
  }

  _clickCalendarDayHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    const isShadow = e.currentTarget.attributes["data-shadow"];
    if (isShadow) return;

    const date = e.currentTarget.attributes["data-date"].toString();

    if (this.mode === "single") {
      this._selectSingleDay(date);
    } else if (this.mode === "range") {
      this._selectRangeDay(date);
    }
  }

  _setupCalendarEvents() {
    for (let i = 0; i < this.contentDays.length; i += 1) {
      this.contentDays[i].addEventListener("click", this._clickCalendarDayHandler.bind(this))
    }
  }

  _setNextMonth() {
    const currentDate = new Date(this.state.currentDate);
    const currentDateWithNextMonth = new Date(
      currentDate.setMonth(currentDate.getMonth() + 1)
    ).getTime();
    this.setState({ currentDate: currentDateWithNextMonth });
  }

  _setPreviousMonth() {
    const currentDate = new Date(this.state.currentDate);
    const currentDateWithPreviousMonth = new Date(
      currentDate.setMonth(currentDate.getMonth() - 1)
    ).getTime();
    this.setState({ currentDate: currentDateWithPreviousMonth });
  }

  _isDateLessThenOther(date1, date2) {
    const d1 = new Date(date1).getTime();
    const d2 = new Date(date2).getTime();
    return d1 < d2;
  }

  _getMonth(date) {
    const _monthList = ["январь", "февраль", "март", "апрель",
      "май", "июнь", "июль", "август", "сентябрь",
      "окрябрь", "ноябрь", "декабрь"];

    return _monthList[new Date(date).getMonth()];
  }

  _getYear(date) {
    return new Date(date).getFullYear();
  }

  _getDaysNumberInMonth(date) {
    const _date = new Date(date)
    _date.setMonth(new Date(date).getMonth() + 1);
    _date.setDate(0);
    return _date.getDate();
  }

  _getFirstDayOfWeek(date) {
    const _date = new Date(date)
    _date.setDate(1);
    const day = _date.getDay();

    if (day === 0) return 6; // day is sunday
    return day - 1;
  }

  _getCalendarDays(date) {
    const month = new Date(date).getMonth();
    const year = new Date(date).getFullYear();
    const daysNumber = this._getDaysNumberInMonth(date);
    const firstDayOfWeek = this._getFirstDayOfWeek(date);
    const daysNumberPreviosMonth = this._getDaysNumberInMonth(new Date(date).setMonth(month - 1))

    const days = new Array(7 * 6).fill({ day: 0, isShadow: false });

    // fill main calendar
    let day = 1;
    for (let i = firstDayOfWeek; i < (daysNumber + firstDayOfWeek); i++) {
      days[i] = { day, date: new Date(year, month, day), isShadow: false };
      day += 1;
    }

    // fill after main calendar
    day = 1;
    for (let i = (daysNumber + firstDayOfWeek); i < (7 * 6); i += 1) {
      days[i] = { day, date: new Date(year, month + 1, day), isShadow: true };
      day += 1;
    }

    // fill before main calendar
    if (firstDayOfWeek !== 0) {
      day = daysNumberPreviosMonth;
      for (let i = firstDayOfWeek - 1; i >= 0; i -= 1) {
        days[i] = { day, date: new Date(year, month - 1, day), isShadow: true };
        day -= 1;
      }
    }

    const isLastRowShadowed = days.slice(-7).filter(({ isShadow }) => isShadow === false).length === 0;
    if (isLastRowShadowed) return days.slice(0, -7);
    // if (isLastRowShadowed) console.log(days.slice(0, -7));

    return days;
  }

  setState(newState) {
    this.state = Object.assign(this.state, newState);

    this._render();
  }

  _render() {
    // render header
    const currentMonth = this._getMonth(this.state.currentDate);
    const currentYear = this._getYear(this.state.currentDate);
    this.headerDateElem.innerHTML = `${currentMonth} ${currentYear}`;

    // render calendar days
    const days = this._getCalendarDays(this.state.currentDate);

    for (let i = 0; i < days.length; i += 1) {
      this.contentDays[i].innerHTML = `<div>${days[i].day}</div>`;
      this.contentDays[i].classList = ["calendar__content-cell"];
      this.contentDays[i].attributes["data-date"] = days[i].date;
      this.contentDays[i].attributes["data-shadow"] = days[i].isShadow;

      if (days[i].isShadow) {
        this.contentDays[i].classList.add(this.classCellDisabled);
      }
    }
    
    const fiveWeekDays = 5 * 7;
    // last week days was shadowed (hiden them)
    if (days.length === fiveWeekDays) {
      for (let i = 35; i < 42; i += 1) {
        this.contentDays[i].classList.add(this.classCellHidden);
      }
    }

    if (this.state.singleDateSelected !== null) {
      days.forEach(({ date }, index) => {
        if (date.toString() === this.state.singleDateSelected) {
          this.contentDays[index]
            .classList.add(this.classCellSingleSelected);
        }
      });
    }

    const range = this.state.range;
    if (range.dateLeft !== null) {
      days.forEach(({ date, isShadow }, index) => {
        if (date.toString() === range.dateLeft && isShadow === false) {
          this.contentDays[index]
            .classList.add(this.classCellRangeLeftSelected);
        }
      });
    }

    if (range.dateRight !== null) {
      days.forEach(({ date, isShadow }, index) => {
        if (date.toString() === range.dateRight && isShadow === false) {
          this.contentDays[index]
            .classList.add(this.classCellRangeRightSelected);
        }
      });
    }

    if (
      this.mode === modes.range
      && range.dateLeft !== null
      && range.dateRight !== null
    ) {
      days.forEach(({ date, isShadow }, index) => {
        if (
          this._isDateLessThenOther(range.dateLeft, date)
          && this._isDateLessThenOther(date, range.dateRight)
          && isShadow === false
        ) {
          this.contentDays[index]
            .classList.add(this.classCellRangeMiddleSelected);
        }
      });
    }
  }
}

// new Calendar({
//   classId: "calendar-1",
//   mode: modes.range,
//   onSelected: (result) => console.log(result)
// });

export {
  Calendar,
  modes
}