import "../buttons";
import "../top_bar_user";

import "./style.scss";

function topBarInit() {
  const switchButton = document.querySelector(".top-bar__switch-button");
  const topBar = document.querySelector(".top-bar");

  switchButton.addEventListener("click", (e) => {
    e.preventDefault();

    topBar.classList.toggle("top-bar_close");
  });

  topBar.classList.toggle("top-bar_close");
}

topBarInit();