import "./style.scss";
import "./dropdown_item";

import { pluralize } from "numeralize-ru";

export const types = {
  rooms: "rooms",
  guests: "guests",
}

const pluralizeNouns = {
  "гость": ["гость", "гостя", "гостей"],
  "младенец": ["младенец", "младенца", "младенцев"],
  "спальни": ["спальня", "спальни", "спален"],
  "кровати": ["кровать", "кровати", "кроватей"],
  "ванные комнаты": ["ванная комната", "ванных комнаты", "ванных комнат"],
}

class Dropdown {
  constructor({ classId, type, defaultMessage, onChange }) {
    if (type === undefined)
      throw new Error(`you should specify dropdown type: "rooms" or "guests"`);

    this.classId = classId;
    this.type = type;
    this.defaultMessage = defaultMessage;

    this.onChange = onChange;

    this.classDropdownOpen = "dropdown_open";
    this.classActionDisabled = "dropdown-item__action_disabled";
    this.classInvisible = "invisible";

    // fields: `fieldName`: { `value`: 0, `max`: number }
    this.state = {
      isOpen: false,
      fields: [],
    }

    this._domInit();
  }

  _domInit() {
    this.dropdownElem = document.querySelector(`.${this.classId}`);

    if (this.dropdownElem === null)
      throw new Error(`dropdown with class ${this.classId} not found`);

    this.headerElem = document.querySelector(`.${this.classId} .dropdown__header`);

    this.fieldValuesElem = [];
    this.fieldActionsElem = [];

    this.itemsLength = document.querySelectorAll(`.${this.classId} .dropdown__content-item`).length
    for (let i = 0; i < this.itemsLength; i += 1) {
      const field = this._getFieldAttributes(i);
      this.state.fields.push(field);

      const fieldValueElem = document.querySelector(
        `.${this.classId} .dropdown__content-item:nth-child(${i + 1}) .dropdown-item__actions .dropdown-item__action-value`
      );

      this.fieldValuesElem.push(fieldValueElem);

      this._setupFieldActionEvents(i);
    }

    const contentActions = document
      .querySelector(`.${this.classId} .dropdown__content-actions`);
    if (contentActions !== null) {
      this.contentActionCleanElem = document
        .querySelector(`.${this.classId} .dropdown__content-action-clean`);
      this.contentActionApplyElem = document
        .querySelector(`.${this.classId} .dropdown__content-action-apply`);

      this._setupContentActionsEvents();
    }

    this._setupHeaderElemEvents();

    this._render();
  }

  hideDropdown() {
    this._setState({ isOpen: false });
  }

  _setState(newState) {
    this.state = Object.assign(this.state, newState);
    this.onChange && this.onChange(this.state.fields);
    this._render();
  }

  _getFieldAttributes(index) {
    const actionAttributes = document.querySelector(
      `.${this.classId} .dropdown__content-item:nth-child(${index + 1}) .dropdown-item`
    ).attributes;

    const name = actionAttributes["data-name"].value;
    const max = Math.floor(actionAttributes["data-max"].value, 10);

    return { name, max, value: 0 };
  }

  _setupHeaderElemEvents() {
    this.headerElem.addEventListener("click", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this._setState({ isOpen: !this.state.isOpen });
    })
  }

  _setupFieldActionEvents(index) {
    const actionButtons = document.querySelectorAll(
      `.${this.classId} .dropdown__content-item:nth-child(${index + 1}) .dropdown-item__actions button`
    );

    this.fieldActionsElem.push(actionButtons);

    const [substractButton, addButton] = actionButtons;

    const max = this.state.fields[index].max;
    const increment = (i) => i >= max ? i : i += 1;
    const decrement = (i) => i > 0 ? i -= 1 : i;

    substractButton.addEventListener("click", e => this._fieldActionHandler(e, index, decrement));
    addButton.addEventListener("click", e => this._fieldActionHandler(e, index, increment));
  }

  _setupContentActionsEvents() {
    this.contentActionCleanElem.addEventListener("click", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this._cleanFields();
    });

    this.contentActionApplyElem.addEventListener("click", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this._applyDropdown();
    });
  }

  _fieldActionHandler(e, index, mutator) {
    e.preventDefault();
    e.stopPropagation();

    const fields = this.state.fields;
    fields[index].value = mutator(fields[index].value);

    this._setState({ fields });
  }

  _applyDropdown() {
    this._setState({ isOpen: false });
  }

  _cleanFields() {
    const fields = this.state.fields.map(field => {
      field.value = 0;
      return field;
    });

    this._setState({ fields });
  }

  _isFieldsClean() {
    return this.state.fields.filter(({ value }) => value !== 0).length === 0;
  }

  _capitalizeHeaderMessage(str) {
    return str
      .split(" ")
      .map(chunk => chunk[0].toUpperCase() + chunk.slice(1, chunk.length))
      .join(" ");
  }

  _buildHeaderMessage() {
    const fields = this.state.fields;

    if (this.type === types.rooms) {
      const message = fields.map(field => {
        const noun = pluralize(field.value, ...pluralizeNouns[field.name]);
        if (field.value > 0) return `${field.value} ${noun}, `;
        else return ""
      }).reduce((acc, data) => acc + data, "").slice(0, -2)

      return message;
    }

    if (this.type === types.guests) {
      let guests = 0;
      let toddlers = 0;

      for (const field of fields) {
        if (field.name === "младенцы") {
          toddlers += field.value;
        } else {
          guests += field.value;
        }
      }

      const guestsNoun = pluralize(guests, ...pluralizeNouns["гость"]);

      if (toddlers > 0) {
        const toddlersNoun = pluralize(toddlers, ...pluralizeNouns["младенец"]);

        return `${guests} ${guestsNoun}, ${toddlers} ${toddlersNoun}`;
      }

      return `${guests} ${guestsNoun}`;
    }

    return this.defaultMessage;
  }

  _render() {
    if (this.state.isOpen) {
      this.dropdownElem.classList.add(this.classDropdownOpen);
    } else {
      this.dropdownElem.classList.remove(this.classDropdownOpen);
    }

    this.state.fields.forEach((field, index) => {
      this.fieldValuesElem[index].innerHTML = field.value;

      this.fieldActionsElem[index][0].classList.remove(this.classActionDisabled);
      this.fieldActionsElem[index][1].classList.remove(this.classActionDisabled);

      if (field.value <= 0) {
        this.fieldActionsElem[index][0].classList.add(this.classActionDisabled);
      }

      if (field.value >= field.max) {
        this.fieldActionsElem[index][1].classList.add(this.classActionDisabled);
      }
    });

    if (this.contentActionCleanElem && this._isFieldsClean()) {
      this.contentActionCleanElem.classList.add(this.classInvisible);
    }

    if (this.contentActionCleanElem && !this._isFieldsClean()) {
      this.contentActionCleanElem.classList.remove(this.classInvisible);
    }

    if (!this._isFieldsClean()) {
      this.headerElem.innerHTML = this._buildHeaderMessage();
    } else {
      this.headerElem.innerHTML = this.defaultMessage;
    }
  }
}

export default Dropdown;

