import { Calendar, modes } from "../calendar/index";

import "../text_field";
import "./style.scss";

export const textFieldModes = {
  single: "single",
  double: "double"
}

const monthsRus = [
  "янв",
  "фев",
  "мар",
  "апр",
  "май",
  "июн",
  "июл",
  "авг",
  "сен",
  "окт",
  "ноя",
  "дек"
];

class DateRange {
  constructor({ classId, onCalendarOpen, onSelected, textFieldMode = textFieldModes.double }) {
    this.classId = classId;

    this.calendar = new Calendar({
      classId: classId + "-calendar",
      mode: modes.range,
      onSelected: this._applyCalendarRange.bind(this),
      onClean: this._cleanCalendar.bind(this)
    });

    this.textFieldMode = textFieldMode;

    this.onCalendarOpen = onCalendarOpen;
    this.onSelected = onSelected;

    this.classCalendarOpen = "date-range_calendar-open";

    this.state = {
      isCalendarOpen: false,
      dateLeft: null,
      dateRight: null,
    };

    this._domInit();
  }

  _domInit() {
    this.rootElem = document.querySelector(`.${this.classId}`);

    if (this.rootElem === null) throw new Error(`DataRange error: element with id .${this.classId} not found`);

    const inputs = document.querySelectorAll(`.${this.classId} input`);

    this.leftDateInputElem = inputs[0];
    this.rightDateInputElem = inputs[1];
    
    if (this.textFieldMode === textFieldModes.double && this.rightDateInputElem === null)
      throw new Error("DateRange error: not found rightDateInputElem in double mode");

    this._setupInputsEvents();

    this._render();
  }

  _setupInputsEvents() {
    this.leftDateInputElem.addEventListener("focus", (e) => {
      e.preventDefault();
      e.stopPropagation();

      this._showCalendar();
    });

    if (this.textFieldMode === textFieldModes.double) {
      this.rightDateInputElem.addEventListener("focus", (e) => {
        e.preventDefault();
        e.stopPropagation();

        this._showCalendar();
      });
    }
  }

  _addHideCalendarEvents() {
    const layout = document.createElement("div");
    layout.classList.add("js-calendar-event-layout");
    layout.setAttribute("style", `width: 100%; height: 100%; z-index: 1; position: fixed; top: 0;`);

    layout.addEventListener("mousedown", this._hideCalendar.bind(this));

    document.body.prepend(layout);
  }

  _cleanHideCalendarEvents() {
    const layout = document.querySelector(".js-calendar-event-layout");
    layout.removeEventListener("mousedown", this._hideCalendar.bind(this));
    layout.remove();
  }

  _cleanCalendar() {
    this._updateState({
      dateLeft: null,
      dateRight: null
    });
  }

  _showCalendar() {
    if (this.state.isCalendarOpen) return;

    this._addHideCalendarEvents();
    this._updateState({ isCalendarOpen: true });
  }

  _hideCalendar() {
    console.log("hide calendar");
    this._cleanHideCalendarEvents();
    this._updateState({ isCalendarOpen: false });
  }

  _applyCalendarRange({ dateLeft, dateRight }) {
    this._updateState({ dateLeft, dateRight });
    this.onSelected && this.onSelected({ dateLeft, dateRight });
    this._hideCalendar();
  }

  _updateState(newState) {
    if (newState.isCalendarOpen === true) {
      this.onCalendarOpen && this.onCalendarOpen();
    }

    this.state = Object.assign(this.state, newState);
    this._render();
  }

  _render() {
    if (this.state.isCalendarOpen) {
      this.rootElem.classList.add("date-range_calendar-open");
    } else {
      this.rootElem.classList.remove("date-range_calendar-open");
    }

    if (this.state.dateLeft !== null) {
      const date = new Date(this.state.dateLeft);
      const value =  this.textFieldMode === textFieldModes.single
        ? `${date.getDate()} ${monthsRus[date.getMonth()]}`
        : `${date.getDate()}.${date.getMonth()}.${date.getFullYear()}`


      console.log(value);
      this.leftDateInputElem.value = value;
    }

    if (this.state.dateLeft === null) {
      this.leftDateInputElem.value = "";
    }

    if (this.state.dateRight !== null) {
      const dateLeft = new Date(this.state.dateLeft);
      const dateRight = new Date(this.state.dateRight);
      if (this.textFieldMode === textFieldModes.double) {
        this.rightDateInputElem.value =
          `${dateRight.getDate()}.${dateRight.getMonth()}.${dateRight.getFullYear()}`
      } else {
        this.leftDateInputElem.value =
          `${dateLeft.getDate()} ${monthsRus[dateLeft.getMonth()]} - ${dateRight.getDate()} ${monthsRus[dateRight.getMonth()]}`
      }
    }

    if (this.state.dateRight === null) {
      if (this.textFieldMode === textFieldModes.double) {
        this.rightDateInputElem.value = "";
      } else {
        console.log("modify left for single handler");
      }
    }
  }
}

export default DateRange;

