import DateRange from "../date_range";
import Dropdown, { types } from "../dropdown"

import "../buttons";
import "./style.scss";

export default function roomSelectorFormInit() {
  const formEl = document.querySelector(".room-selector-form");

  const formSubmitHandler = (e) => {
    e.preventDefault();
    e.stopPropagation();

    console.log("submit form");
  }

  formEl.addEventListener("submit", formSubmitHandler);

  const dropdown = new Dropdown({
    classId: "id-dropdown-room-selector",
    type: types.guests,
    defaultMessage: "Сколько гостей"
  });

  const calendarOpenHandler = () => {
    dropdown.hideDropdown();
  }

  new DateRange({
    classId: "id-date-range-room-selector",
    onCalendarOpen: calendarOpenHandler
  });
}